<template>
	<div class="test">
	<section class="py-5 text-center container vh-100">
		<div style="margin-top: 150px">
			<h1 style="font-size: 80px">
				<b>Where is My Home</b>
			</h1>
		</div>
			<div class="py-lg-5">
				<div class="row col-md-12 justify-content-center mb-2">
					<div class="form-group col-md-2">
						<select class="form-select bg-secondary text-light" id="sido"
							name="sido">
							<option value="">시도 선택</option>
						</select>
					</div>
					<div class="form-group col-md-2">
						<select class="form-select bg-secondary text-light" id="gugun"
							name="gugun">
							<option value="">구군 선택</option>
						</select>
					</div>
					<div class="form-group col-md-2">
						<select class="form-select bg-secondary text-light" id="dong"
							name="dong">
							<option value="">동 선택</option>
						</select>
					</div>
					<div class="form-group col-md-2">
						<button type="submit" id="list-btn" class="btn btn-dark">
							아파트매매정보</button>
					</div>
				</div>
			</div>
	</section>
	</div>
</template>

<style scoped>
  .test {
    background: url("../assets/background.jpg");
    background-size: cover;
  }
</style>


<script>
import axios from 'axios'
export default {
  name: "HomeView",
  props: {
    msg: String,
  }
};

new Vue({
  el: '#app',
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      axios.get('/address/sido') // 서블릿의 URL
        .then(response => {
          var data = response.data;
          var options = '<option value="">시도 선택</option>';
          for (var i = 0; i < data.length; i++) {
            options += '<option value="' + data[i] + '">' + data[i] + '</option>';
          }
          this.$refs.sido.innerHTML = options; // city select 요소의 option 값을 업데이트
        })
        .catch(error => {
          alert('An error occurred while processing your request: ' + error);
        });
    }
  }
});



</script>