import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    alias: ['/index', '/main'],
    name: 'home',
    component: HomeView
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
